body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

.delete {
  position: relative;
  float: right;
}
.btn {
  height: 25px;
  width: 25px;
  margin: 5px;
  border:0;
  text-align: center;
  border-radius: 50%;
  text-transform: none;
  display: inline-block;
}

.App{
  margin: 20px auto;
  width: 90%;
  max-width: 700px;
}

.navbar{
  padding: 10px 20px;
  text-align: center;

}

.navbar h1{
  margin: 10px 0;
}


.book-list {
  margin: 20px;
  padding-bottom: 20px;
}

.book-list ul {
  padding: 0;
  list-style-type: none;
}

.book-list li {
  border-radius: 20px;
  padding: 10px;
  cursor: pointer;
  margin: 10px 0;
 overflow-wrap: break-word;
}

.book-list li:hover, .book-list li:active{
  opacity: 0.7;
  text-decoration: line-through;
}

.book-list .title {
  font-weight: bold;
  font-size: 1em;
  float: left;
  
}

.title:focus{
  text-decoration: line-through;
  opacity: 0.7;
}

.empty {
  margin: 20px;
  padding: 20px;
  text-align: center;
}

form {
  padding: 20px;
}

input[type='text'] {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  margin: 6px 0;
  border: 0;
}

input[type='submit'] {
  padding: 6px 20px;
  margin: 10px auto;
  height:auto;
  width: auto;
  font-size: larger;
  border: 0;
  border-radius: 30px;
  display: block;
}

